
<template>
  <div class="vachangemain">
      <div class="tjfx_tool">
        <div class="tjfx_bar">
          <i class="icon-changzhanzs"></i>
          电压调整
        </div>
        <div class="tjfx_tools">
          <el-select
            v-model="value1"
            multiple
            collapse-tags
            style="margin-left: 20px;"
            placeholder="发电厂"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-select v-model="value2" collapse-tags style="margin-left: 20px;" placeholder="线路">
            <el-option
              v-for="item in options1"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-select v-model="value3" collapse-tags style="margin-left: 20px;" @change="getData()" placeholder="变压器">
            <el-option
              v-for="item in options2"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-button style="margin-left:20px;" @click='getData()'>筛选</el-button>
        </div>
      </div>
      <div class="tops">
        <div class="tableleft">
          <el-table :data="tableData" style="width: 100%" size="mini" class="vachange_tables">
            <el-table-column prop="date" label="变压器名称"></el-table-column>
            <el-table-column prop="name" label="属性值"></el-table-column>
          </el-table>
          <el-table  style="width: 100%" size="mini" class="vachange_tables">
            <el-table-column label="电压调整指南">
              <el-table-column ></el-table-column>
            </el-table-column>
          </el-table>
        </div>
        <div class="echtright">
          <div class="dlqx_boxs" id="dlqx"></div>
        </div>
      </div>
  </div>
</template>

<script>
var data = [
  ["00:00", 116],
  ["01:30", 129],
  ["03:00", 135],
  ["04:30", 86],
  ["06:00", 73],
  ["07:30", 85],
  ["09:00", 73],
  ["10:30", 68],
  ["12:00", 92],
  ["13:30", 130],
  ["15:00", 245],
  ["16:30", 139],
  ["18:00", 115],
  ["19:30", 111],
  ["21:00", 309],
  ["22:30", 206],
  ["24:00", 137]
];
var dataone = [
  ["00:00", 50],
  ["01:30", 110],
  ["03:00", 130],
  ["04:30", 86],
  ["06:00", 73],
  ["07:30", 85],
  ["09:00", 73],
  ["10:30", 68],
  ["12:00", 92],
  ["13:30", 10],
  ["15:00", 25],
  ["16:30", 160],
  ["18:00", 100],
  ["19:30", 190],
  ["21:00", 300],
  ["22:30", 220],
  ["24:00", 110]
];
var datatwo = [
  ["00:00", 90],
  ["01:30", 110],
  ["03:00", 110],
  ["04:30", 86],
  ["06:00", 92],
  ["07:30", 85],
  ["09:00", 73],
  ["10:30", 68],
  ["12:00", 92],
  ["13:30", 10],
  ["15:00", 60],
  ["16:30", 160],
  ["18:00", 100],
  ["19:30", 190],
  ["21:00", 300],
  ["22:30", 220],
  ["24:00", 110]
];
var dateList = data.map(function(item) {
  return item[0];
});
var valueList = data.map(function(item) {
  return item[1];
});
var valueListone = dataone.map(function(item) {
  return item[1];
});
var valueListtwo = datatwo.map(function(item) {
  return item[1];
});
export default {
  data() {
    return {
      data,
      dateList,
      valueList,
      valueListone,
      valueListtwo,
      isFull: false,
      tableData: [
        {
          date: "配变名称",
          name: "电网_栗溪9#箱变"
        },
        {
          date: "配变型号",
          name: "ZGS11"
        },
        {
          date: "额定容量",
          name: "630"
        },
        ,
        {
          date: "额定电流",
          name: "957.19"
        },
        {
          date: "出厂时间",
          name: "2009/04/10"
        },
        {
          date: "地区/供电单位",
          name: "国网荆门市供电公司"
        },
        {
          date: "档案信息",
          name: "2"
        },
        {
          date: "所属馈线",
          name: "电网_10kV栗马线栗16"
        },
        {
          date: "供电半经",
          name: "9.55"
        },
        {
          date: "绕线方式",
          name: "Dyn11"
        }
      ],
      tableDataone: [
        {
          date: "越限日期",
          name: "2019-08-09"
        },
        {
          date: "电压最小值/V",
          name: "144.8"
        }
      ],
      options: [
        {
          value: "1",
          label: "高新栗溪供电所"
        }
      ],
      value1: ['1'],
      options1: [
        {
          value: "1",
          label: "电网_10kV栗马线栗16"
        }
      ],
      value2: "1",
      options2: [
        {
          value: "马集3#",
          label: "马集3#"
        },
        {
          value: "栗溪9#",
          label: "栗溪9#"
        },
        {
          value: "栗溪9#",
          label: "栗溪9#"
        }
      ],
      value3: "马集3#"
    };
  },
  mounted() {
    // this.chart();
    this.getData()
  },
  methods: {
    cancel() {
      this.$emit("cancel", false);
    },
    chart(res) {
      //异常分布图
      var zbfldb_data = this.echarts.init(document.getElementById("dlqx"));
      var zbfldb_option = {
        visualMap: [
          {
            show: false,
            type: "continuous",
            seriesIndex: 0,
            min: 0,
            max: 400
          },
          {
            show: false,
            type: "continuous",
            seriesIndex: 1,
            dimension: 0,
            min: 0,
            max: dateList.length - 1
          }
        ],

        title: [
          {
            textStyle: {
              color: "#fff"
            },
            left: "left",
            text: "电网_栗溪11#"
          },
          {}
        ],
        tooltip: {
          trigger: "axis"
        },
        xAxis: [
          {
            axisLabel: {
              show: true,
              lineStyle: "#FFF",
              textStyle: {
                color: "#fff",
                fontSize: "12"
              }
            },
            nameTextStyle: {
              color: "#fff"
            },
            data: dateList
          }
        ],
        yAxis: [
          {
            axisLabel: {
              show: true,
              lineStyle: "#FFF",
              textStyle: {
                color: "#fff",
                fontSize: "12"
              }
            },
            nameTextStyle: {
              color: "#fff"
            },
            splitLine: { show: false }
          }
        ],
        series: [
          {
             name:'电压',
            type: "line",
            showSymbol: false,
            data: valueList
          },
          {
            name:'功率',
            type: "line",
            showSymbol: false,
            data: valueListtwo
          },
          {
            name:'电流',
            type: "line",
            showSymbol: false,
            data: valueListone
          }
        ]
      };
      zbfldb_data.setOption(zbfldb_option);
    },
    getData1(type){
      return this.$http.get("/lineMonitor/getPowerLineSmooth",{
        params:{
          courtName:encodeURI(this.value3),
          threePhase:type
        }
      })
    },
    getData(){
      var that =this
      this.$http.all([this.getData1('V'),this.getData1('I'),this.getData1('P')]).
      then(this.$http.spread(function (acct,perms,thrit){
           valueList = []
           dateList=[]
           valueListtwo=[]
           valueListone=[]
          acct.data.data.forEach(item=>{
           dateList.push(item.time)
           valueList.push(item.value)
          })
          perms.data.data.forEach(item=>{
           valueListone.push(item.value)
          })
          thrit.data.data.forEach(item=>{
           valueListtwo.push(item.value)
          })
          that.chart()
      }))
      
    }
  }
};
</script>

<style>
/* 隐藏横向滚动条 */
.el-scrollbar__wrap {
  overflow-x: hidden !important;
}

/* element滚动条样式 */
.el-scrollbar__bar {
  position: absolute;
  right: 2px;
  bottom: 2px;
  z-index: 1;
  border-radius: 4px;
  opacity: 1;
  -webkit-transition: opacity 120ms ease-out;
  transition: opacity 120ms ease-out;
}
.vachangemain {
  width: 95%;
  margin: 0 auto;
}
.title {
  height: 20px;
  width: 100%;
}
.tops {
  width: 100%;
  margin-top: 12px;
}
.tableleft {
  width: 35%;
  float: left;
}
.echtright {
  width: 65%;
  float: right;
}
.dlqx_boxs {
  width: 960px;
  height: 60vh;
  margin: 0 auto;
}
.tablemain {
  width: 100%;
}
.tablemain table td {
  text-align: center;
}

@media screen and (min-width: 1380px) {
  .vascrol {
    height: 800px;
  }
  .tops {
    width: 100%;
  }
  .tableleft {
    width: 35%;
    float: left;
  }
  .echtright {
    width: 65%;
    float: right;
  }
}
@media screen and (max-width: 1399px) {
  .vascrol {
    height: 580px;
  }
  .tops {
    width: 100%;
  }
  .tableleft {
    width: 25%;
    float: left;
  }
  .echtright {
    width: 75%;
    float: right;
  }
}
</style>