
<template>
  <div class="vachangemain">
    <div class="tjfx_tool">
      <div class="tjfx_bar"></div>
      <div class="tjfx_tools">
        <el-select
          v-model="value1"
          multiple
          collapse-tags
          style="margin-left: 20px;"
          placeholder="发电厂"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-select v-model="value2" collapse-tags style="margin-left: 20px;" placeholder="线路">
          <el-option
            v-for="item in options1"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-button style="margin-left:20px;" @click="getData()">查询</el-button>
      </div>
    </div>
    <div class="mxjy_wrap">
      <div class="echrartleft">
        <div class="tjfx_bar">
          <i class="icon-changzhanzs"></i>
          模型治理曲线图
        </div>
        <div class="mxjy_dlqx" id="dlqx"></div>
      </div>
      <div class="tablesright">
        <el-table
          :data="tableData"
          style="width: 100%"
          height="480"
          border
          size="mini"
          class="vachange_tables"
        >
          <el-table-column type="index" label="序号"></el-table-column>
          <el-table-column prop="sub" label="所属厂站"></el-table-column>
          <el-table-column prop="lineName" label="所属线路"></el-table-column>
          <el-table-column prop="name" label="设备名称"></el-table-column>
          <el-table-column prop="id" label="设备编号"></el-table-column>
          <el-table-column prop="isFair" label="是否匹配"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <div>
                <el-button size="small" icon="el-icon-edit">关联</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          sub: "",
          lineName: "",
          name: "电网_栗溪9#箱变",
          id: "",
          isFair: "否"
        },
        {
          sub: "",
          lineName: "",
          name: "电网_栗溪9#箱变",
          id: "",
          isFair: "否"
        },
        {
          sub: "",
          lineName: "",
          name: "电网_栗溪9#箱变",
          id: "",
          isFair: "否"
        },
        {
          sub: "",
          lineName: "",
          name: "电网_栗溪9#箱变",
          id: "",
          isFair: "否"
        }
      ],
      options: [
        {
          value: "1",
          label: "高新栗溪供电所"
        }
      ],
      value1: ["1"],
      options1: [
        {
          value: "1",
          label: "电网_10kV栗马线栗16"
        }
      ],
      value2: "1",
      options2: [
        {
          value: "马集3#",
          label: "马集3#"
        },
        {
          value: "栗溪9#",
          label: "栗溪9#"
        },
        {
          value: "栗溪9#",
          label: "栗溪9#"
        }
      ],
      value3: "马集3#"
    };
  },
  mounted() {
    this.chart();
    this.getData();
  },
  methods: {
    cancel() {
      this.$emit("cancel", false);
    },
    chart() {
      var cjcgfg_box = this.echarts.init(document.getElementById("dlqx"));
      var cjcgfg_data = {
        // backgroundColor: "none",
        tooltip: {
          trigger: "axis"
        },
        grid: {
          top: "16%",
          left: "0%",
          right: "2%",
          bottom: "0%",
          containLabel: true
        },
        legend: {
          data: ["模型治理情况"]
        },
        calculable: true,
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: ["1", "2", "3", "4", "5", "6", "7", "8"]
          }
        ],
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "模型治理情况",
            type: "line",
            areaStyle: {
              normal: {
                type: "default",
                color: "white"
              }
            },
            smooth: true,
            itemStyle: {
              normal: {
                areaStyle: {
                  type: "default"
                }
              }
            },
            data: [76, 79, 80, 86, 88, 89, 89]
          }
        ]
      };
      cjcgfg_box.setOption(cjcgfg_data);
    }
  }
};
</script>

<style lang='scss'>
.mxjy_wrap {
  display: flex;
}
.echrartleft {
  border: 1px solid transparent;
  @include border-color();
  width: 35%;
  margin-top: 12px;
}
.tablesright {
  flex: 1;
  margin-left: 18px;
}
.mxjy_dlqx {
  width: 100%;
  height: 480px;
}
</style>