<style>
/* 隐藏横向滚动条 */
.el-scrollbar__wrap {
  overflow-x: hidden !important;
}
/* element滚动条样式 */
.el-scrollbar__bar {
  position: absolute;
  right: 2px;
  bottom: 2px;
  z-index: 1;
  border-radius: 4px;
  opacity: 1;
  -webkit-transition: opacity 120ms ease-out;
  transition: opacity 120ms ease-out;
}
.vachange_tables {
  margin-top: 12px;
}
.vachange_tables table tr th {
  font-size: 15px;
  font-weight: normal;
  color: #ccc;
}
.vachangemain {
  width: 95%;
  margin: 0 auto;
}
.title {
  height: 20px;
  width: 100%;
}
.tops {
  width: 100%;
  margin-top: 12px;
}
.tablemain {
  width: 100%;
}
.tablemain table td {
  text-align: center;
}
</style>
<template>
  <div class="vachangemain">
    <div class="tjfx_tool">
      <div class="tjfx_bar">
        <i class="icon-changzhanzs"></i>
        阈值调整
      </div>
      <div class="tjfx_tools">
        <el-select
          v-model="value1"
          multiple
          collapse-tags
          style="margin-left: 20px;"
          placeholder="发电厂"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-select v-model="value2" collapse-tags style="margin-left: 20px;" placeholder="线路">
          <el-option
            v-for="item in options1"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-button style="margin-left:20px;" @click='getData()'>查询</el-button>
      </div>
    </div>
    <div class="tops">
        <el-table :data="tableData" style="width: 100%" size="mini" border class="vachange_tables">
          <el-table-column type='index' label="序号"></el-table-column>
          <el-table-column prop="lineName" label="线路名称"></el-table-column>
          <el-table-column prop="localName" label="变压器名称"></el-table-column>
          <el-table-column prop="lineId" label="id"></el-table-column>
          <el-table-column prop="cecThreshold" label="电流"></el-table-column>
          <el-table-column prop="volThreshold" label="电压"></el-table-column>
          <el-table-column prop="powThreshold" label="功率"></el-table-column>
          <el-table-column label="操作">
               <template slot-scope="scope">
                <div>
                  <el-button size='small' icon="el-icon-edit" @click="close=true;obj=scope.row">修改</el-button>
                  <el-button size='small'  type="danger" icon="el-icon-delete" @click="obj=scope.row;deletedData()">删除</el-button>
                </div>
              </template>
          </el-table-column>
        </el-table>
    </div>
    <overlay :close.sync="close">
      <div class="add_trans">
        <div class="sblb_bar">
          <i class="icon-changzhanzs"></i>
          {{obj.localName}}
          <div class="fr sbbt_tool">
            <i class="icon-error sblbguabi" @click="close=false"></i>
          </div>
        </div>
        <div class="add_main">
          <el-form ref="form" label-position="left" label-width="90px">
            </el-form-item>
            <el-form-item label="电压">
              <el-input v-model="obj.volThreshold" placeholder="请输入电压" style="width:220px;"></el-input>
            </el-form-item>
             <el-form-item label="电流">
              <el-input v-model="obj.cecThreshold" placeholder="请输入电流" style="width:220px;"></el-input>
            </el-form-item>
            <el-form-item label="功率">
              <el-input v-model="obj.powThreshold" placeholder="请输入功率" style="width:220px;"></el-input>
            </el-form-item>
            <!-- <el-form-item label="修改日期">
              <el-date-picker type="date" placeholder="选择日期" v-model="form.date"  ></el-date-picker>
            </el-form-item> -->
              <el-button type="primary" @click="saveData()">修改</el-button>
              <el-button @click="close=false"> 取消</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </overlay>
  </div>
</template>

<script>
import overlay from "@/components/overlay";
export default {
  data() {
    return {
      isFull: false,
      tableData: [
        {
          date: "配变名称",
          name: "电网_栗溪11#箱变"
        },
        {
          date: "配变型号",
          name: "ZGS11"
        },
        {
          date: "额定容量",
          name: "630"
        },
        ,
        {
          date: "额定电流",
          name: "957.19"
        },
        {
          date: "出厂时间",
          name: "2009/04/10"
        },
        {
          date: "地区/供电单位",
          name: "国网荆门市供电公司"
        },
        {
          date: "档案信息",
          name: "2"
        },
        {
          date: "所属馈线",
          name: "电网_10kV栗马线栗16"
        },
        {
          date: "供电半经",
          name: "9.55"
        },
        {
          date: "绕线方式",
          name: "Dyn11"
        }
      ],
      options: [
        {
          value: "1",
          label: "高新栗溪供电所"
        }
      ],
      value1: ["1"],
      options1: [
        {
          value: "530C24CD-1CFC-478B-B287-35FDEF7BFDE6-64950",
          label: "电网_10kV栗马线栗16"
        }
      ],
      value2: "530C24CD-1CFC-478B-B287-35FDEF7BFDE6-64950",
      obj:{},
      close:false
    };
  },
  mounted() {
    this.getData()
  },
  methods: {
    cancel() {
      this.$emit("cancel", false);
    },
    getData(){
      this.$http.get('/lineMonitor/getWarningThresholdList',{
        params:{
          lineId:this.value2
        }
      }).then(res=>{
        if(res.data.code ===1){
          this.tableData=res.data.data
        }
      })
    },
    saveData(){
      this.$http.post('lineMonitor/updateWarningThreshold',{
        equipmentId:this.obj.equipmentId,
        volThreshold:this.obj.volThreshold,
        powThreshold:this.obj.cecThreshold,
        cecThreshold:this.obj.powThreshold,
        lineId:this.obj.lineId,
        id:this.obj.id
      }).then(res=>{
        if(res.data.code ===1 ){
          this.close = false;
          this.getData()
          this.$message({
          message: '阀值修改成功！',
          type: 'success'
        });
        }
      })
    },
    deletedData(){
      this.$confirm('是否删除？', '确认信息', {
          distinguishCancelAndClose: true,
          confirmButtonText: '删除',
          cancelButtonText: '取消'
        }).then(()=>{
            this.$http.post('/lineMonitor/deleteWarningThreshold?id='+this.obj.id).then(res=>{
          if(res.data.code ===1){
           this.getData()
           this.$message({
             message: '阀值删除成功！',
              type: 'success'
            });
          }
          })
      })
    }
  },
  components:{overlay}
};
</script>

