import Vue from 'vue'
import App from './App.vue'
import router from './router'
import '@babel/polyfill'
import ElementUI from 'element-ui'
// import './styles.scss'
import "@/assets/fonts/iconfont.js";
import "@/assets/scss/base.scss";
import plugin from '@/utils/install'// global-components
import * as filters from '@/utils/filters'// global-filters
import echarts from "echarts";
import axios from 'axios'

Vue.use(ElementUI)
Vue.prototype.echarts = echarts;
Vue.prototype.$http = axios
Vue.config.productionTip = false
Vue.use(plugin)
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})
Vue.prototype.$http.get("serverconfig.json").then((result) => {
  axios.defaults.baseURL = result.data.ApiUrl;
}).catch((error) => {
  axios.defaults.baseURL = "http://10.172.246.229:8080/system-service"
});
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
