
<template>
  <div class="vachangemain">
    <div class="tjfx_tool">
      <div class="tjfx_bar"></div>
      <div class="tjfx_tools">
        <label>查看类型：</label>
        <el-button type="primary">电压</el-button>
        <el-button type="success">功率</el-button>
        <el-button type="warning">电流</el-button>
      </div>
    </div>
    <div class="fxjc_wrap">
      <div class="unit_item">
        <div class="device_general">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-shudianxianlu" />
          </svg>
          <span>10kV栗马线栗16</span>
          <el-progress :percentage="percentage" :color="customColorMethod"></el-progress>
          <p>51.79kW/81.60kW</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      percentage: 60,
      options: [
        {
          value: "1",
          label: "电压"
        },
        {
          value: "2",
          label: "功率"
        },
        {
          value: "3",
          label: "电流"
        }
      ],
      value1: "1"
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    cancel() {
      this.$emit("cancel", false);
    },
    customColorMethod(percentage) {
      if (percentage < 30) {
        return "#909399";
      } else if (percentage < 70) {
        return "#e6a23c";
      } else {
        return "#67c23a";
      }
    }
  }
};
</script>

<style lang='scss'>
.fxjc_wrap {
  display: flex;
  border: 1px solid transparent;
  @include border-color();
  @include box-shadow();
  box-sizing: border-box;
  padding: 12px;
  margin-top: 20px;
}
.unit_item {
  border-radius: 8px;
  padding: 8px;
  @include box-shadow();
  background-color:$--color-primary;
}
</style>